import { Component, OnInit, Inject } from '@angular/core';
import { Observable } from 'rxjs';
import { Blog } from '../blog';
import { ActivatedRoute, Router, ParamMap } from '@angular/router';
import { BlogService } from '../blog.service';
import { switchMap } from 'rxjs/operators';
import { AuthService } from '../../auth/auth.service';
import { Title, Meta } from '@angular/platform-browser';
import { PLATFORM_ID } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';

@Component({
  selector: 'app-blog-detail',
  templateUrl: './blog-detail.component.html',
  styleUrls: ['./blog-detail.component.scss']
})
export class BlogDetailComponent implements OnInit {

  blog$: Observable<Blog>;

  public user: any;

  constructor(
    private authService: AuthService,
    private route: ActivatedRoute,
    private router: Router,
    private blogService: BlogService,
    private titleService: Title,
    private metaService: Meta,
    @Inject(PLATFORM_ID) private platformId: Object
  ) { }

  ngOnInit() {
    this.authService.me().subscribe(data => {
      this.user = data.user;
    });
    this.getBlogById()
  }

  getBlogById() {
    this.blog$ = this.route.paramMap.pipe(
      switchMap((params: ParamMap) =>
        this.blogService.getBlogById(params.get('id'))
      )
    )
    this.blog$.subscribe(blog=>{
      this.titleService.setTitle( blog.title );
      this.metaService.updateTag({name: 'description', content: blog.title})
      let keyWords = (this.metaService.getTag('name= "keywords"') && this.metaService.getTag('name= "keywords"').content) || 'åæ°æ¹æ³,åæ°é©¿ç«,åæ°é©¿è·¯,åæ°äºä»¶,åæ°,åæ°çäºæ,åæ°æ¹æ³è®º';
      keyWords += blog.tags.join(',')
      this.metaService.updateTag({name: 'keywords', content: keyWords})
    })
  }

  deleteBlog(id) {
    if (confirm('ç¡®è®¤è¦å é¤æ­¤blogåï¼')) {
      this.blogService.deleteBlogById(id).subscribe(res => {
        this.router.navigate([`/blogs/`])
      })
    }
  }

  scrollTop() {
    if (isPlatformBrowser(this.platformId)) {
      // Client only code.
      window.scrollTo(0, 0);
    }
  }
}
